import { createContext, useContext, useEffect, useState } from "react";
import axios from "../api/axiosDefaults";
import { useCurrentUser } from "../contexts/CurrentUserContext";

const UserDataContext = createContext();
const SetUserDataContext = createContext();

export const useUserData = () => useContext(UserDataContext);
export const useSetUserData = () => useContext(SetUserDataContext);

export const UserDataProvider = ({ children }) => {
    const [userData, setUserData] = useState({
        pageUser: { results: [] },
        popularUsers: { results: [] },
    });

    const currentUser = useCurrentUser();

    useEffect(() => {
        const handleMount = async () => {
            try {
                const { data } = await axios.get("/api/users/?ordering=-level", {
                    headers: { Authorization: "" },
                });

                setUserData((prevState) => ({
                    ...prevState,
                    popularUsers: data,
                }));
            } catch (err) {
                console.log(err);
            }
        };

        handleMount();
    }, [currentUser]);

    return (
        <UserDataContext.Provider value={userData}>
            <SetUserDataContext.Provider value={setUserData}>
                {children}
            </SetUserDataContext.Provider>
        </UserDataContext.Provider>
    );
};
